import React, { useState, useEffect } from 'react';
import { View, Text, FlatList, TouchableOpacity, TextInput, ScrollView, StyleSheet } from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';

const CartScreen = ({ navigation }) => {
  const [cartItems, setCartItems] = useState([]);
  const [savedItems, setSavedItems] = useState([]);
  const [totalPrice, setTotalPrice] = useState(0);
  const [PINCHIK, setPromoCode] = useState('');
  const [discount, setDiscount] = useState(0);
  const [newItem, setNewItem] = useState({ id: 0, name: '', quantity: 1 });

  useEffect(() => {
    loadCartItems();
    loadSavedItems();
  }, []);

  useEffect(() => {
    calculateTotalPrice();
  }, [cartItems]);

  const loadCartItems = async () => {
    try {
      const items = await AsyncStorage.getItem('cartItems');
      if (items)
        {
          setCartItems(JSON.parse(items));
        }
      } catch (error) {
        console.error('Ошибка при загрузке данных из AsyncStorage:', error);
      }
    };

    const loadSavedItems = async () => {
      try {
        const items = await AsyncStorage.getItem('savedItems');
        if (items) {
          setSavedItems(JSON.parse(items));
        }
      } catch (error) {
        console.error('Ошибка при загрузке данных из AsyncStorage:', error);
      }
    };

    const updateItemQuantity = (item, quantity) => {
      const updatedItems = cartItems.map((cartItem) => {
        if (cartItem.id === item.id) {
          return { ...cartItem, quantity };
        }
        return cartItem;
      });
      setCartItems(updatedItems);
      saveCartItems(updatedItems);
      calculateTotalPrice(updatedItems);
    };
  
    const removeItem = (item) => {
      if (cartItems.some(cartItem => cartItem.id === item.id)) {
        const updatedItems = cartItems.filter((cartItem) => cartItem.id !== item.id);
        setCartItems(updatedItems);
        saveCartItems(updatedItems);
        calculateTotalPrice(updatedItems);
      } else if (savedItems.some(savedItem => savedItem.id === item.id)) {
        const updatedItems = savedItems.filter((savedItem) => savedItem.id !== item.id);
        setSavedItems(updatedItems);
        saveSavedItems(updatedItems);
      }
    };
  
    const moveToCart = (item) => {
      if (savedItems.some(savedItem => savedItem.id === item.id)) {
        const updatedSavedItems = savedItems.filter(savedItem => savedItem.id !== item.id);
        const newCartItem = { ...item}; 
        const updatedCartItems = [...cartItems, newCartItem];
        
        setCartItems(updatedCartItems);
        setSavedItems(updatedSavedItems);
        saveCartItems(updatedCartItems);
        saveSavedItems(updatedSavedItems);
        calculateTotalPrice(updatedCartItems);
      }
    };
  
    const moveToSaved = (item) => {
      if (cartItems.some(cartItem => cartItem.id === item.id)) {
        const updatedCartItems = cartItems.filter(cartItem => cartItem.id !== item.id);
        const updatedSavedItems = [...savedItems, item]; 
  
        setCartItems(updatedCartItems);
        setSavedItems(updatedSavedItems);
        saveCartItems(updatedCartItems);
        saveSavedItems(updatedSavedItems);
        calculateTotalPrice(updatedCartItems);
      }
    };

    const addToCart = () => {
      if (newItem.name.trim() !== '' && newItem.quantity > 0) {
        const newPrice = Math.floor(Math.random() * 150) + 1; // Random price between 1 and 150
        const existingItem = cartItems.find(item => item.name === newItem.name);
        if (existingItem) {
          updateItemQuantity(existingItem, existingItem.quantity + newItem.quantity);
        } else {
          const updatedItems = [...cartItems, { ...newItem, price: newPrice, id: Date.now() }];
          setCartItems(updatedItems);
          saveCartItems(updatedItems);
          calculateTotalPrice(updatedItems);
        }
        setNewItem({ id: 0, name: '', quantity: 1 });
      }
    };
  
    const calculateTotalPrice = () => {
      const total = cartItems.reduce((acc, item) => acc + (item.price * item.quantity), 0);
      setTotalPrice(total);
    };
  
    const saveCartItems = async (items) => {
      try {
        await AsyncStorage.setItem('cartItems', JSON.stringify(items));
      } catch (error) {
        console.error('Ошибка при сохранении данных в AsyncStorage:', error);
      }
    };

    const saveSavedItems = async (items) => {
      try {
        await AsyncStorage.setItem('savedItems', JSON.stringify(items));
      } catch (error) {
        console.error('Ошибка при сохранении данных в AsyncStorage:', error);
      }
    };

    const applyPromoCode = () => {
      const discountPercentage = 10; 
      const discountAmount = totalPrice * (discountPercentage / 100);
      setDiscount(discountAmount);
    };
  
    const handleCheckout = () => {
      // Переход на экран оформления заказа с передачей параметров
      navigation.navigate('Order', { cartItems, totalPrice });
    };
  
    const renderCartItem = ({ item }) => (
      <View style={styles.itemContainer}>
        <Text style={styles.itemName}>{item.name}</Text>
        <View style={styles.quantityContainer}>
          <TouchableOpacity
            style={styles.quantityButton}
            onPress={() => updateItemQuantity(item, item.quantity - 1)}
            disabled={item.quantity <= 1} 
          >
            <Text style={styles.quantityButtonText}>-</Text>
          </TouchableOpacity>
          <Text style={styles.itemQuantity}>{item.quantity}</Text>
          <TouchableOpacity
            style={styles.quantityButton}
            onPress={() => updateItemQuantity(item, item.quantity + 1)}
          >
            <Text style={styles.quantityButtonText}>+</Text>
          </TouchableOpacity>
        </View>
        <Text style={styles.itemPrice}>${(item.price * item.quantity).toFixed(2)}</Text>
        <TouchableOpacity
          style={styles.deleteButton}
          onPress={() => removeItem(item)}
        >
          <Text style={styles.deleteButtonText}>Delete</Text>
        </TouchableOpacity>
        <TouchableOpacity
          style={styles.moveToSavedButton}
          onPress={() => moveToSaved(item)} 
        >
          <Text style={styles.moveToSavedButtonText}>В сохраненныу</Text>
        </TouchableOpacity>
      </View>
    );

    const renderSavedItem = ({ item }) => (
      <View style={styles.itemContainer}>
        <Text style={styles.itemName}>{item.name}</Text>
        <Text style={styles.itemPrice}>${item.price.toFixed(2)}</Text>
        <TouchableOpacity
          style={styles.deleteButton}
          onPress={() => removeItem(item)}
        >
          <Text style={styles.deleteButtonText}>Delete</Text>
        </TouchableOpacity>
        <TouchableOpacity
          style={styles.moveToSavedButton}
          onPress={() => moveToCart(item)}
        >
          <Text style={styles.moveToSavedButtonText}>В корзину</Text>
        </TouchableOpacity>
      </View>
    );

    return (
      <View style={styles.container}>
        <Text style={styles.title}>Корзина</Text>
        <View style={styles.newItemContainer}>
          <TextInput
            style={styles.newItemInput}
            placeholder="Name of product"
            value={newItem.name}
            onChangeText={(text) => setNewItem({ ...newItem, name: text })}
          />
          <TextInput
            style={styles.newItemInput}
            placeholder="РљРѕР»РёС‡РµСЃС‚РІРѕ"
            keyboardType="numeric"
            value={newItem.quantity.toString()}
            onChangeText={(text) => setNewItem({ ...newItem, quantity: parseInt(text) })}
            />
            <TouchableOpacity style={styles.newItemButton} onPress={addToCart}>
              <Text style={styles.newItemButtonText}>Добваить</Text>
            </TouchableOpacity>
          </View>
          <ScrollView contentContainerStyle={{ flexGrow: 1 }}>
            <View style={styles.savedItemsContainer}>
              {/* Сохраненные товары */}
              <View style={styles.basketContainer}>
                <Text style={styles.savedItemsTitle}>Сохраненные</Text>
                <FlatList
                  data={savedItems}
                  keyExtractor={(item) => item.id.toString()}
                  renderItem={renderSavedItem}
                  contentContainerStyle={styles.listContainer}
                />
              </View>

              {/* Корзина */}
              <View style={styles.deferredContainer}>
                <Text style={styles.savedItemsTitle}>Корзина</Text>
                <FlatList
                  data={cartItems}
                  keyExtractor={(item) => item.id.toString()}
                  renderItem={renderCartItem}
                  contentContainerStyle={styles.listContainer}
                />
              </View>
            </View>
          </ScrollView>
          <View style={styles.promoContainer}>
          <TextInput
            style={styles.promoInput}
            placeholder="Enter the promo code"
            value={PINCHIK}
            onChangeText={setPromoCode}
          />
          <TouchableOpacity style={styles.promoButton} onPress={applyPromoCode}>
            <Text style={styles.promoButtonText}>Применить</Text>
          </TouchableOpacity>
        </View>
          <View style={styles.totalContainer}>
            <Text style={styles.totalLabel}>Итого:</Text>
            <Text style={styles.totalPrice}>${(totalPrice - discount).toFixed(2)}</Text>
            <TouchableOpacity style={styles.checkoutButton} onPress={handleCheckout}>
              <Text style={styles.checkoutButtonText}>Place the order</Text>
            </TouchableOpacity>
  
            <TouchableOpacity
              style={styles.historyButton}
              onPress={() => navigation.navigate('OrderHistory')}
           >
              <Text style={styles.historyButtonText}>Orders History</Text>
          </TouchableOpacity>
          </View>
        </View>
      );
    };

    const styles = StyleSheet.create({
      container: {
        flex: 1,
        backgroundColor: '#FFFFFF',
        paddingHorizontal: 16,
        paddingVertical: 24,
      },
      title: {
        fontSize: 24,
        fontWeight: 'bold',
        marginBottom: 16,
        color: '#9b4ff2',
      },
      listContainer: {
        flex: 1,
        flexGrow: 1,
        paddingVertical: 8,
      },
      itemContainer: {
        flexDirection: 'column',
        alignItems: 'flex-start',
        justifyContent: 'space-between',
        paddingVertical: 10,
        borderBottomWidth: 1,
        borderBottomColor: '#fa1387',
      },
      itemName: {
        fontSize: 16,
        flex: 1,
      },
      quantityContainer: {
        flexDirection: 'row',
        alignItems: 'center',
      },
      quantityButton: {
        backgroundColor: '#7be5ff',
        paddingHorizontal: 10,
        paddingVertical: 5,
        borderRadius: 5,
      },
      quantityButtonText: {
        fontSize: 16,
      },
      itemQuantity: {
        marginHorizontal: 8,
        fontSize: 16,
      },
      itemPrice: {
        fontSize: 16,
        fontWeight: 'bold',
      },
      deleteButton: {
        backgroundColor: '#ff3b30',
        paddingHorizontal: 10,
        paddingVertical: 5,
        borderRadius: 5,
      },
      deleteButtonText: {
        color: '#fff',
        fontSize: 14,
      },
      moveToSavedButton: {
        backgroundColor: '#7be5ff',
        paddingHorizontal: 10,
        paddingVertical: 5,
        borderRadius: 5,
        marginTop: 8,
      },
      moveToSavedButtonText: {
        fontSize: 14,
      },
      promoContainer: {
        flexDirection: 'row',
        alignItems: 'center',
        marginBottom: 16,
      },
      promoInput: {
        flex: 1,
        borderWidth: 1,
        borderColor: '#2582f4',
        borderRadius: 4,
        paddingHorizontal: 12,
        paddingVertical: 8,
        marginRight: 8,
      },
      promoButton: {
        backgroundColor: '#007aff',
        paddingHorizontal: 12,
        paddingVertical: 8,
        borderRadius: 4,
      },
      promoButtonText: {
        color: '#fff',
        fontSize: 14,
      },
      newItemContainer: {
        flexDirection: 'row',
        alignItems: 'center',
        marginBottom: 16,
      },
      newItemInput: {
        flex: 1,
        borderWidth: 1,
        borderColor: '#2582f4',
        borderRadius: 4,
        paddingHorizontal: 12,
        paddingVertical: 8,
        marginRight: 8,
      },
      newItemButton: {
        backgroundColor: '#007aff',
        paddingHorizontal: 12,
        paddingVertical: 8,
        borderRadius: 4,
      },
      splitContainer: {
        flexDirection: 'column',
        gap:"100px",
        justifyContent: 'space-between',
      },
      basketContainer: {
        height:"250px",
        flex: 1,
        paddingRight: 8, 
      },
      deferredContainer: {
        height:"250px",
        flex: 1,
        paddingLeft: 8, 
      },
      divider: {
        width: "25px", // Width of the divider
        backgroundColor: '#e0e0e0', // Color of the divider
        marginHorizontal: 8, // Space around the divider
      },
      newItemButtonText: {
        color: '#fff',
        fontSize: 14,
      },
      savedItemsContainer: {   
        flex: 1,
        flexDirection: 'column',
        marginTop: 16,
        borderTopWidth: 1,
        borderTopColor: '#e0e0e0',
        paddingTop: 16,
      },
      savedItemsTitle: {
        fontSize: 18,
        fontWeight: 'bold',
        marginBottom: 8,
      },
      totalContainer: {
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
        marginTop: 16,
        paddingVertical: 12,
        borderTopWidth: 1,
        borderTopColor: '#e0e0e0',
      },
      checkoutButton: {
        backgroundColor: '#28a745',
        padding: 15,
        borderRadius: 5,
        alignItems: 'center',
        marginTop: 20,
      },
      checkoutButtonText: {
        color: '#fff',
        fontSize: 16,
        fontWeight: 'bold',
      },
      historyButton: {
        backgroundColor: '#007bff',
        padding: 15,
        borderRadius: 5,
        alignItems: 'center',
        marginTop: 20,
      },
      historyButtonText: {
        color: '#fff',
        fontSize: 16,
        fontWeight: 'bold',
      },
      totalLabel: {
        fontSize: 24,
        fontWeight: 'bold',
      },
      totalPrice: {
        fontSize: 24,
        fontWeight: 'bold',
      },
      savedItemsContainer: {
        flex: 1, // Занимает всё доступное пространство
        flexDirection: 'column', // Распределяет дочерние элементы по вертикали
      },
      basketContainer: {
        flex: 1, // Занимает половину экрана
        padding: 16,
        backgroundColor: '#f9f9f9', // Для наглядности (можно убрать)
      },
      deferredContainer: {
        flex: 1, // Занимает вторую половину экрана
        padding: 16,
        backgroundColor: '#ffffff', // Для наглядности (можно убрать)
      },
      savedItemsTitle: {
        fontSize: 16,
        fontWeight: 'bold',
        marginBottom: 8,
      },
      listContainer: {
        flexGrow: 1,
      },
    });
    
  export default CartScreen;  
  
  