import React, { useEffect, useState } from 'react';
import { View, Text, FlatList, TouchableOpacity, StyleSheet } from 'react-native';
import axios from 'axios'; 

const OrderHistoryScreen = ({ route }) => {
  const { cartItems, deliveryDate, totalPrice, coordinates } = route.params;

  const [orders, setOrders] = useState([]);
  let lastID = 0;

  useEffect(() => {
    fetchOrders();
  }, []);

  const fetchOrders = async () => {
    try {
      const response = await axios.get('https://jsonplaceholder.typicode.com/posts');
      setOrders(response.data);
    } catch (error) {
      console.error('Ошибка при получении заказов:', error);
    }
    const storedOrders = await AsyncStorage.getItem('orders');
    if (storedOrders) {
      setOrders(JSON.parse(storedOrders)); 
    }
  };

  const handleDeleteOrder = async (orderId) => {
    try {
      await axios.delete(`https://jsonplaceholder.typicode.com/posts/${orderId}`);
      const updatedOrders = orders.filter(order => order.id !== orderId);
      setOrders(updatedOrders);
    } catch (error) {
      console.error('Ошибка при удалении заказа:', error);
    }
  };

  const renderItem = ({ item }) => {
    lastID += 1
    return (
      <View style={styles.orderItem}>
      <Text style={styles.orderText}>Order #{Date.now() + Math.floor(Math.random() + 1000)}</Text>
      <Text style={styles.orderText}>User: {item.userId || 'Не указано'}</Text>
      <Text style={styles.orderText}>Name: {item.title || 'Не указано'}</Text>
      <Text style={styles.orderText}>Total Price: ${totalPrice.toFixed(2) || 'Не указано'}</Text> 
      <Text style={styles.orderText}>Items: {cartItems.map(i => i.name).join(', ') || 'Не указано'}</Text> 
      <Text style={styles.orderText}>Data: {new Date(deliveryDate).toLocaleDateString() || 'Не указано'}</Text>
      <Text style={styles.orderText}>Coordinates: {`(${coordinates[0].toFixed(6)}, ${coordinates[1].toFixed(6)})` || 'Не указано'}</Text>

      <TouchableOpacity
        style={styles.deleteButton}
        onPress={() => handleDeleteOrder(item.id)}
      >
        <Text style={styles.deleteButtonText}>Delete</Text>
      </TouchableOpacity>
    </View>
    )
  };

  return (
    <View style={styles.container}>
      <Text style={styles.title}>Orders History</Text>
      <FlatList
        data={orders}
        keyExtractor={item => item.id.toString()}
        renderItem={renderItem}
      />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    padding: 20,
  },
  title: {
    fontSize: 24,
    fontWeight: 'bold',
    marginBottom: 20,
  },
  orderItem: {
    padding: 15,
    backgroundColor: '#fff',
    borderRadius: 5,
    marginBottom: 10,
    shadowColor: '#000',
    shadowOffset: { width: 0, height: 2 },
    shadowOpacity: 0.1,
    shadowRadius: 4,
    elevation: 2,
  },
  orderText: {
    fontSize: 16,
    marginBottom: 10,
  },
  deleteButton: {
    backgroundColor: '#dc3545',
    padding: 10,
    borderRadius: 5,
    alignItems: 'center',
  },
  deleteButtonText: {
    color: '#fff',
    fontWeight: 'bold',
  },
});

export default OrderHistoryScreen;