import React, { useState } from 'react';
import { View, Text, TextInput, TouchableOpacity, StyleSheet, Alert } from 'react-native';
import { YMaps, Map, Placemark } from 'react-yandex-maps';
import AsyncStorage from '@react-native-async-storage/async-storage';
import axios from 'axios';

const OrderScreen = ({ route, navigation }) => {
  const { cartItems, totalPrice } = route.params;

  const [deliveryDate, setDeliveryDate] = useState(new Date().toISOString().split('T')[0]);
  const [coordinates, setCoordinates] = useState([55.751244, 37.618423]);

  const validateDate = (date) => {
    const today = new Date();
    const inputDate = new Date(date);
    return inputDate >= today;
  };

  const handleOrderSubmit = async () => {
    if (!cartItems || cartItems.length === 0) {
      console.error('Нет товаров в корзине для размещения заказа.');
      return;
    }

    if (!validateDate(deliveryDate)) {
      Alert.alert('Ошибка', 'Не корректная дата доставки');
      return;
    }

    const orderData = {
      items: cartItems,
      totalPrice,
      deliveryDate,
      coordinates,
    };

    try {
      const response = await axios.post('https://jsonplaceholder.typicode.com/posts', orderData);
      console.log('Заказ размещен:', response.data);

      const existingOrders = JSON.parse(await AsyncStorage.getItem('orders')) || [];
      existingOrders.push(orderData); // Сохраняем созданный заказ 
      await AsyncStorage.setItem('orders', JSON.stringify(existingOrders));
      console.log('Заказы обновлены в AsyncStorage:', existingOrders);

      navigation.navigate('OrderHistory', { cartItems, deliveryDate, totalPrice, coordinates });
    } catch (error) {
      console.error('Ошибка при размещении заказа:', error);
    }
  };

  return (
    <View style={styles.container}>
      <Text style={styles.title}>Выбор данных о доставке</Text>

      <Text>Введите дату доставки (YY:MM:DD):</Text>
      <TextInput
        style={styles.dateInput}
        placeholder="YY:MM:DD"
        value={deliveryDate}
        onChangeText={setDeliveryDate}
        maxLength={10} // Ограничиваем длину ввода
      />

      <Text>Select the delivery location on the map:</Text>
      <YMaps>
        <Map
          defaultState={{ center: coordinates, zoom: 9 }}
          width="100%"
          height="300px"
          onClick={(e) => setCoordinates(e.get('coords'))}
        >
          <Placemark geometry={coordinates} />
        </Map>
      </YMaps>

      <Text>Select the location: {`(${coordinates[0].toFixed(6)}, ${coordinates[1].toFixed(6)})`}</Text>

      <TouchableOpacity style={styles.submitButton} onPress={handleOrderSubmit}>
        <Text style={styles.submitButtonText}>Place an order</Text>
      </TouchableOpacity>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    padding: 16,
    backgroundColor: '#f5f5f5',
  },
  title: {
    fontSize: 24,
    fontWeight: 'bold',
    marginBottom: 16,
  },
  dateInput: {
    width: '100%',
    borderColor: '#ccc',
    borderWidth: 1,
    padding: 10,
    borderRadius: 4,
    marginVertical: 20,
  },
  submitButton: {
    backgroundColor: '#28a745',
    padding: 16,
    borderRadius: 4,
    marginTop: 16,
  },
  submitButtonText: {
    color: '#fff',
    fontSize: 18,
    fontWeight: 'bold',
    textAlign: 'center',
  },
});

export default OrderScreen;